/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState, useCallback, useRef } from "react";
import project from "./assets/project.png";
import ig from "./assets/ig.svg";
import twit from "./assets/twitter.svg";
import link from "./assets/link.png";
import phone from "./assets/phone.png";
import laptop from "./assets/laptop.png";
import AOS from "aos";
import "aos/dist/aos.css";
import Form from "./component/form";
import { useTransition, animated } from "react-spring";
import styles from "./animation";

function home() {
  const ref = useRef([])
  const [items, set] = useState([]);
  const transitions = useTransition(items, {
    from: {
      opacity: 0,
      height: 0,
      innerHeight: 0,
      transform: 'perspective(600px) rotateX(0deg)',
      color: '#8fa5b6',
    },
    enter: [
      { opacity: 1, height: 80, innerHeight: 80 },
      { transform: 'perspective(600px) rotateX(180deg)', color: '#28d79f' },
      { transform: 'perspective(600px) rotateX(0deg)' },
    ],
    leave: [{ color: '#c23369' }, { innerHeight: 0 }, { opacity: 0, height: 0 }],
    update: { color: '#28b4d7' },
  })

  const reset = useCallback(() => {
    ref.current.forEach(clearTimeout)
    ref.current = []
    set([])
    ref.current.push(setTimeout(() => set(['Hi im', 'Danendra']), 2000))
    ref.current.push(setTimeout(() => set(['Rofl1st', 'Danendra']), 5000))
    ref.current.push(setTimeout(() => set(['Click Me!', 'Please']), 8000))
  }, [])

  useEffect(() => {
    reset()
    return () => ref.current.forEach(clearTimeout)
  }, [])
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
  }, []);
  const [show, setShow] = React.useState(false);
  const [navbarOpen, setNavbarOpen] = React.useState(false);
  // item

  
  return (
    <React.Fragment>
      <div className=" antialiased  pt-4 sm:py-8 overflow-x-hidden">
        {/* navbar */}
        <div className="flex justify-center">
          <div className="flex items-center justify-between z-20  w-11/12">
            <h1 className="sm:text-5xl text-3xl text-white logo">
              ROFL<span className="text-red-700">1</span>ST
            </h1>
            <div>
              <svg
                onClick={() => setNavbarOpen(!navbarOpen)}
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6   text-white flex mx-auto sm:hidden"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
                  clipRule="evenodd"
                />
              </svg>

              <ul className="hidden sm:flex text-sm mr-7 mt-0 text-white">
                <li className="mr-7 hover:opacity-75 ">
                  <a href="#home">Home</a>
                </li>
                <li className="mr-7 hover:opacity-75">
                  <a href="#skills">Skills</a>
                </li>
                <li className="mr-7 hover:opacity-75">
                  <a href="#services">Services</a>
                </li>
                <li className="hover:opacity-75">
                  <a href="#project">My Project</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div
          className={
            "flex flex-grow items-center sm:hidden" +
            (navbarOpen ? " flex" : " hidden")
          }
          id="example-navbar-danger"
        >
          <ul className="flex flex-col lg:flex-row list-none lg:ml-auto">
            <li className="nav-item">
              <a
                className="px-3 py-2 flex items-center text-xs   leading-snug text-white hover:opacity-75"
                href="#home"
              >
                <i className="fab fa-facebook-square  leading-lg text-white opacity-75"></i>
                <span className="ml-2">Home</span>
              </a>
            </li>
            <li className="nav-item">
              <a
                className="px-3 py-2 flex items-center text-xs   leading-snug text-white hover:opacity-75"
                href="#skills"
              >
                <i className="fab fa-twitter  leading-lg text-white opacity-75"></i>
                <span className="ml-2">Skills</span>
              </a>
            </li>
            <li className="nav-item">
              <a
                className="px-3 py-2 flex items-center text-xs   leading-snug text-white hover:opacity-75"
                href="#services"
              >
                <i className="fab fa-pinterest  leading-lg text-white opacity-75"></i>
                <span className="ml-2">Services</span>
              </a>
            </li>
            <li className="nav-item">
              <a
                className="px-3 py-2 flex items-center text-xs   leading-snug text-white hover:opacity-75"
                href="#project"
              >
                <i className="fab fa-pinterest  leading-lg text-white opacity-75"></i>
                <span className="ml-2">My Project</span>
              </a>
            </li>
          </ul>
        </div>
        {/* navbar */}
        {/* Top */}
        <div
          id="home"
          className="flex sm:flex-row flex-col justify-center sm:justify-between items-center w-5/6 mx-auto mt-1 sm:mt-5"
        >
          <img
            data-aos={"zoom-out-right"}
            className="project"
            src={project}
            alt=""
          />
          <div data-aos={"zoom-out-left"} className="top w-full sm:w-1/4 ">
            <h1 className="text-7xl text-white pt-10 sm:pt-4 cursor-pointer">
            {transitions(({ innerHeight, ...rest }, item) => (
          <animated.div className={styles.transitionsItem} style={rest} onClick={reset}>
            <animated.div style={{ overflow: 'hidden', height: innerHeight }}>{item}</animated.div>
          </animated.div>
        ))}
            </h1>
            <h2 className="text-2xl sm:text-3xl text-white ">
              design and build your imaginary website
            </h2>
            <p className="text-white text-xs sm:text-lg pt-4">
              I am danendra. experienced web development based on jakarta. i am
              here to help making your dreams come true
            </p>
            <div className="mt-7 sm:mt-9 flex items-center">
              <button
                onClick={() => {
                  setShow(true);
                }}
                className="sm:py-2 sm:px-7 py-1 px-3 bg-red-700 hover:text-red-700 hover:bg-white rounded-md text-white text-base sm:text-xl"
              >
                Hire me
              </button>
              <div className="ml-4 flex">
                <img className="w-10" src={ig} alt="" />
                <img className="w-10" src={twit} alt="" />
                <img className="w-10" src={link} alt="" />
              </div>
            </div>
          </div>
        </div>
        {/* Top */}
        {/* Middle */}
        {/* Mid top */}
        <div id="services" className="midTop mt-16 sm:mt-32 w-full">
          <div
            data-aos={"zoom-in"}
            className="contentTop items-center mx-auto justify-center w-1/2 sm:w-1/4 text-center"
          >
            <h1 className="sm:text-5xl text-2xl text-white pt-20 sm:pt-28 pb-5">
              good product made by skilled developer
            </h1>
            <p className="text-white w-full text-xs sm:text-base sm:w-3/4 mx-auto">
              I believe every good product made by skilled developer, check it
              out!
            </p>
          </div>
          <div className="flex flex-col sm:flex-row items-center m-auto justify-center mt-5 sm:mt-36">
            <img
              data-aos={"fade-right"}
              data-aos-offset={"100"}
              data-aos-easing={"ease-in-sine"}
              src={phone}
              className="sm:pr-96 pr-0 sm:w-auto w-7/12"
              alt=""
            />
            <img
              data-aos={"fade-left"}
              data-aos-offset={"100"}
              data-aos-easing={"ease-in-sine"}
              src={laptop}
              className="sm:w-auto w-7/12 ml-8"
              alt=""
            />
          </div>
        </div>
        {/* Mid top */}
        {/* bottom top */}
        <div id="project" className="midTop">
          <div
            data-aos={"zoom-in"}
            className="contentTop sm:items-center ml-7 sm:ml-16  justify-center sm:w-1/4 text-left"
          >
            <h1 className="sm:text-5xl text-2xl text-white pt-28 pb-5">
              check out
              <br /> my recent project
            </h1>
            <p className="text-white w-10/12 sm:w-3/4 sm:text-base text-xs">
              Here is my recent project. Every project will be deployed out with
              some case study
            </p>
          </div>
          <div className="grid sm:flex items-center justify-center mt-10 sm:mt-36 gap-y-12 sm:gap-y-0 gap-x-48 pb-5 sm:pb-20">
            <div data-aos="fade-up-right" className="box">
              <div className="bg h-64 w-64 sm:h-80 sm:w-80 block">
                <div className="opacity bg-gray-500 bg-opacity-30 h-64 w-64 sm:h-80 sm:w-80">
                  <div className="justify-end flex">
                    <div className="relative border-none px-1 py-1 sm:px-2 sm:py-2 bg-gray-500 ">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="sm:h-7 sm:w-7 h-6 w-6 text-white"
                        viewBox="0 0 20 20"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </div>
                  </div>
                  <h1 className="justify-end cursor-default flex mt-36 sm:mt-40 mr-3 text-gray-500 text-7xl sm:text-9xl">
                    u
                  </h1>
                </div>
              </div>
              <h1 className="sm:text-3xl text-xl pt-2 sm:pt-4 text-white">
                Uyi Project
              </h1>
            </div>
            <div data-aos="fade-down" className="box">
              <div className="bg2 h-64 w-64 sm:h-80 sm:w-80 block ">
                <div className=" opacity bg-gray-500 bg-opacity-30 h-64 w-64 sm:h-80 sm:w-80">
                  <div className="justify-end flex">
                    <div className="relative border-none px-1 py-1 sm:px-2 sm:py-2 bg-gray-500 ">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="sm:h-7 sm:w-7 h-6 w-6 text-white"
                        viewBox="0 0 20 20"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </div>
                  </div>
                  <h1 className="justify-end cursor-default flex mt-36 sm:mt-40 mr-3 text-gray-500 text-7xl sm:text-9xl">
                    p
                  </h1>
                </div>
              </div>
              <h1 className="sm:text-3xl text-xl pt-2 sm:pt-4 text-white">
                Portofolio Project
              </h1>
            </div>
            <div data-aos="fade-up-left" className="box">
              <div className="bg3 h-64 w-64 sm:h-80 sm:w-80 block">
                <div className="opacity bg-gray-500 bg-opacity-30 h-64 w-64 sm:h-80 sm:w-80">
                  <div className="justify-end flex">
                    <div className="relative border-none px-1 py-1 sm:px-2 sm:py-2 bg-gray-500 ">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="sm:h-7 sm:w-7 h-6 w-6 text-white"
                        viewBox="0 0 20 20"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </div>
                  </div>
                  <h1 className="justify-end cursor-default flex mt-36 sm:mt-40 mr-3 text-gray-500 text-7xl sm:text-9xl">
                    I
                  </h1>
                </div>
              </div>
              <h1 className="sm:text-3xl text-xl pt-2 sm:pt-4 text-white">
                IT fest project
              </h1>
            </div>
          </div>
          {/* bottom */}
          <div
            id="skills"
            data-aos="fade-right"
            className="justify-between flex flex-col sm:flex-row sm:items-center items-start  w-10/12 pb-10 sm:pb-24"
          >
            <div className="contentTop items-center sm:ml-16 ml-7  justify-center w-full sm:w-1/4 text-left">
              <h1 className="sm:text-5xl text-2xl text-white pt-28 pb-5">
                My skill i will
                <br /> offer to you
              </h1>
              <p className="text-white sm:text-base text-xs">
                Here is some of my skill that you will enjoy
              </p>
            </div>
            <div className="right sm:text-2xl text-xl mt-10 sm:mt-40 ml-7 sm:ml-0">
              <ul className="grid grid-cols-1 sm:grid-rows-2 sm:grid-flow-col gap-y-4 sm:gap-y-0 gap-x-32 text-white text-right">
                <li className="inline-flex">
                  <span className="text-red-700 sm:text-xl text-lg span mr-2 my-auto">
                    01.
                  </span>{" "}
                  Ui/ux design
                </li>
                <li className="inline-flex">
                  <span className="text-red-700 sm:text-xl text-lg span mr-2 my-auto">
                    02.
                  </span>{" "}
                  fullstack web
                </li>
                <li className="inline-flex">
                  <span className="text-red-700 sm:text-xl text-lg span mr-2 my-auto">
                    03.
                  </span>{" "}
                  android apps
                </li>
                <li className="inline-flex">
                  <span className="text-red-700 sm:text-xl text-lg span mr-2 my-auto">
                    04.
                  </span>{" "}
                  videography
                </li>
              </ul>
            </div>
          </div>
          {/* bottom */}
        </div>
        {/* bottom top */}

        {/* Middle */}
        {/* Bottom da ril */}
        <div className="bottom pt-28 hidden sm:flex justify-center items-center">
          <div className="bg-footer w-10/12">
            <div
              data-aos={"zoom-in"}
              className="contentTop mx-auto justify-center w-1/4 text-center"
            >
              <h1 className="text-5xl text-white pt-14 pb-5 leading-tight">
                got an idea? tell me and i will make it{" "}
                <span className="span">happens!</span>
              </h1>
              <div className="pb-14 pt-5">
                <button
                  onClick={() => {
                    setShow(true);
                  }}
                  className="py-3 px-11  hover:text-red-700 hover:bg-white btn rounded-md text-white text-3xl"
                >
                  hire me
                </button>
              </div>
            </div>
          </div>
        </div>
        {/* Bottom da ril */}
      </div>
      <Form onClose={() => setShow(false)} show={show} setShow={setShow}></Form>
    </React.Fragment>
  );
}

export default home;
