import React from "react";
import "aos/dist/aos.css";
import * as Yup from "yup";
import { Formik } from "formik";
import Error from "../Error";
const RegisterSchema = Yup.object().shape({
  name: Yup.string().required("Fill it, please"),
  email: Yup.string().email().required("Fill it, please"),
  desc: Yup.string().min(8, "Minimum 8 chara").required("Fill it, please"),
});

const Form = (props) => {


  const initialState = {
    name: "",
    email: "",
    desc: "",
  };

  if (!props.show) {
    return null;
  }
  return (
    <div>
      <div
        data-cy="modal-add"
        variant="primary"
        className=" bg-black bg-opacity-50 z-20 modal absolute inset-0 flex justify-center items-center overflow-y-hidden"
      >
        <div
          data-aos={"zoom-in"}
          className="overlay flex sm:w-auto w-full flex-col items-center justify-center"
        >
          <div className="flex flex-col w-9/12 lg:w-auto lg:flex-row">
            <div className="justify-start flex">
              <div
                onClick={props.onClose}
                className="cursor-pointer absolute rounded-tl border-none px-1 py-1 sm:px-2 sm:py-2 h-8 w-8 sm:h-11 sm:w-11 bg-gray-500 "
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="sm:h-7 sm:w-7 w-5 h-5 m-auto text-white"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path
                    fillRule="evenodd"
                    d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                    clipRule="evenodd"
                  />
                </svg>
              </div>
            </div>
            <div className="bg-modal cursor-default sm:py-10 rounded px-10 sm:px-16 pt-16 sm:pt-32">
              <h2 className="text-white text-center text-4xl font-semibold mb-5 tracking-wider lg:text-6xl">
                let’s get to work
              </h2>
              <p className="text-white text-center text-md font-semibold mb-5 tracking-wider lg:text-3xl">
                quick replies
              </p>
            </div>
            <Formik
              initialValues={initialState}
              validationSchema={RegisterSchema}
              enableReinitialize
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleSubmit,
                handleBlur,
                setFieldValue,
                isSubmitting,
              }) => (
                <form
                  name="Feedback form"
                  method="post"
                  className="flex flex-col bg-modal-2 py-5 px-10 rounded lg:w-7/12"
                >
                  <input
                    error={errors.name && touched.name}
                    type="text"
                    id="name"
                    name="name"
                    placeholder="Enter your username"
                    required
                    value={values.name}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    className="py-2 px-4 mb-0 rounded border border-solid border-white text-gray-000 placeholder-gray-000 font-semibold"
                  />
                  {errors.name && touched.name && <Error>{errors.name}</Error>}
                  <input
                    error={errors.email && touched.email}
                    type="email"
                    name="email"
                    id="email"
                    placeholder="Enter your email"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                    required
                    className="py-2 px-4 rounded mt-5 border border-solid border-white text-gray-000 placeholder-gray-000 font-semibold"
                  />
                  {errors.email && touched.email && (
                    <Error>{errors.email}</Error>
                  )}
                  <textarea
                     error={errors.desc && touched.desc}
                     onChange={handleChange}
                     onBlur={handleBlur}
                     value={values.desc}
                    name="desc"
                    id="desc"
                    cols="30"
                    rows="10"
                    placeholder="Enter your wish"
                    required
                    className="py-2 px-4 rounded mt-5 border border-solid border-white text-gray-000 placeholder-gray-000 font-semibold"
                  ></textarea>
                    {errors.desc && touched.desc && (
                    <Error>{errors.desc}</Error>
                  )}
                  <input
                    onClick={props.onClose}
                    type="submit"
                    value="Submit"
                    className="bg-red-700 mt-5 text-white font-bold tracking-wider py-2 rounded cursor-pointer transition-all hover:bg-red-800"
                  />
                </form>
              )}
            </Formik>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Form;
